// Import game states
import GameStateA from 'states/GameStateA'; // -- Form upload image
import GameStateB from 'states/GameStateB'; // -- Load and pick cordinates
import GameStateC from 'states/GameStateC'; // -- Animate


class Game extends Phaser.Game {

	constructor() {
		super('100%', document.body.offsetHeight , Phaser.AUTO, 'content', null, true);
		this.state.add('GameStateA', GameStateA, false);
		this.state.add('GameStateB', GameStateB, false);
		this.state.add('GameStateC', GameStateC, false);
		this.state.start('GameStateA',true, false);
	}

}

const curGame = new Game();
//Previews image file localy 
function previewFile(){
    var preview = document.querySelector('img'); //selects the query named img
    var file    = document.querySelector('input[type=file]').files[0]; //sames as here
    var reader  = new FileReader();

    reader.onloadend = function () {
      preview.src = reader.result;
      curGame.uploadedImage = reader.result;
      console.log('uploaded image do something');
    }

    if (file) {
      reader.readAsDataURL(file); //reads the data as a URL
    } else {
       preview.src = "";
    }
}

// Used for a login page
var uploadedImageTriggered = document.getElementById('uploadedImage');
// console.log(document.getElementById('uploadedImage').src);
if(uploadedImageTriggered){
  uploadedImageTriggered.addEventListener('click', function(){ 
    curGame.uploadedImage = uploadedImageTriggered.src;
    console.log('uploaded image do something');
    curGame.state.start('GameStateA',true, false);
    hideToggle();
  });
}

// Hide class weeklyfixtures
function hideToggle() { $(".uploadForm").hide(); }

