class GameStateC extends Phaser.State {


	create() {
		
		// Add goal sprite
		this.goal = this.add.sprite(this.game.world.centerX, this.game.world.centerY + 170, 'spriteSheet', 'Goal');
		this.goal.anchor.setTo(0.5);

		// Add your teams text sprite
		this.yourTeams = this.add.sprite(this.game.world.centerX, this.game.world.centerY - 120, 'spriteSheet', 'yourTeams');
		this.yourTeams.anchor.setTo(0.5);

		//let tileTextStyle = { font: "28px PT Sans", fill: "#fff", boundsAlignH: "center", boundsAlignV: "middle", align:'center'};
		//this.yourTeams = this.add.text(this.game.world.centerX-80, this.game.world.centerY - 135, 'YOUR TEAMS', tileTextStyle );

		// Create 4 team logos
		this.createTeamLogo(4);
	}

	createTeamLogo(numb){

		// get json for use in populating 
		let data = this.game.cache.getJSON('jsonObject');
		// set stle of text 
		let style = { font: "32px PT Sans", fill: "#fff", boundsAlignH: "center", boundsAlignV: "middle", align:'center'};

		for (let i = 1; i < numb+1; i++) { 		
			// Create group
			let dynamicGroupRef = 'teamGroup'+i;
            this[dynamicGroupRef] = this.game.add.group();
			// Logo background
			let dynamicTeamRef = 'teamLogoBg'+i;
			this[dynamicTeamRef] = this.add.sprite(30, 30,'spriteSheet', 'teamLogo');
			this[dynamicTeamRef].anchor.setTo(0.5);
			// EDL Logo from json
			let dynamicTeamLogoRef = 'teamLogo'+i;
			this[dynamicTeamLogoRef] = this.add.sprite(30, 30,'logos', data.teams[i-1].logo);
			this[dynamicTeamLogoRef].anchor.setTo(0.5);
			this[dynamicTeamLogoRef].scale.setTo(0.85);
			// Text for display name
			let dynamicTextRef = 'teamtext'+i;
			let text = data.teams[i-1].displayName;
			this[dynamicTextRef] = this.add.text(0 , 0, text,style );
			this[dynamicTextRef].anchor.setTo(0.5);
			this[dynamicTextRef].position.setTo(this[dynamicTeamRef].width/2 - this[dynamicTeamRef].width/2 +30, this[dynamicTeamRef].height/2 +50);
			// Shine effect
			let dynamicShineRef = 'teamShine'+i;
			this[dynamicShineRef] = this.add.sprite(30, 30,'shineSpriteSheet');
			this[dynamicShineRef].anchor.setTo(0.5);
			this[dynamicShineRef].animations.add('shine');
			// Add all to group
			this[dynamicGroupRef].add(this[dynamicTeamRef]);
			this[dynamicGroupRef].add(this[dynamicTeamLogoRef]);	
			this[dynamicGroupRef].add(this[dynamicTextRef]);
			this[dynamicGroupRef].add(this[dynamicShineRef]);
			// Hide group
			this[dynamicGroupRef].alpha = 0;
			this[dynamicGroupRef].position.setTo(this.game.world.centerX, this.game.world.centerY);
		}
		// Call animtion of groups
		this.animateInTeams();
	}

	animateInTeams(){

		// Postion team 1 top left 
		let footballInTween = this.add.tween(this.teamGroup1).to( { y: this.game.world.centerY -55, x:this.game.world.centerX -150, alpha:1 }, 500, "Linear", true, 0, 0);
		footballInTween.onComplete.add(function(){
			// play shine animtion
			this.teamShine1.animations.play('shine',24, false);
		}, this);
		footballInTween.start();
		let footballInScaleTween = this.add.tween(this.teamGroup1.scale).to( {x:1, y:1 }, 1000, "Linear", true, 0, 0);
		footballInScaleTween.start();

		// Postion team 2 bottom left
		let footballInTween2 = this.add.tween(this.teamGroup2).to( { y: this.game.world.centerY +145, x:this.game.world.centerX -150, alpha:1 }, 500, "Linear", true, 1000, 0);
		footballInTween2.onComplete.add(function(){
			// play shine animtion
			this.teamShine2.animations.play('shine',24, false);
		}, this);
		footballInTween2.start();
		let footballInScaleTween2 = this.add.tween(this.teamGroup2.scale).to( {x:1, y:1 }, 1000, "Linear", true, 1000, 0);
		footballInScaleTween2.start();

		// Postion team 3 top right
		let footballInTween3 = this.add.tween(this.teamGroup3).to( { y: this.game.world.centerY -55, x:this.game.world.centerX +90, alpha:1 }, 500, "Linear", true, 2000, 0);
		footballInTween3.onComplete.add(function(){
			// play shine animtion
			this.teamShine3.animations.play('shine',24, false);
		}, this);
		footballInTween3.start();
		let footballInScaleTween3 = this.add.tween(this.teamGroup3.scale).to( {x:1, y:1 }, 1000, "Linear", true, 2000, 0);
		footballInScaleTween3.start();

		// Postion team 4 bottom right
		let footballInTween4 = this.add.tween(this.teamGroup4).to( { y: this.game.world.centerY +145, x:this.game.world.centerX +90, alpha:1 }, 500, "Linear", true, 3000, 0);
		footballInTween4.onComplete.add(function(){
			// play shine animtion
			this.teamShine4.animations.play('shine',24, false);
			this.teamShine4.animations.currentAnim.onComplete.add(function () {	
				this.anitmePreNextState();
			}, this);
		}, this);
		footballInTween4.start();
		let footballInScaleTween4 = this.add.tween(this.teamGroup4.scale).to( {x:1, y:1 }, 1000, "Linear", true, 3000, 0);
		footballInScaleTween4.start();
	}

	anitmePreNextState(){

		// Hide Your Teams text
		let yourTeamsTween = this.add.tween(this.yourTeams).to( { alpha:0 }, 500, "Linear", true, 0, 0);
		yourTeamsTween.start();

		// Move each team to next stage for results page and hide text
		let footballInTween = this.add.tween(this.teamGroup1).to( { y: this.game.world.centerY -117, x:this.game.world.centerX -200 }, 500, "Linear", true, 0, 0);
		footballInTween.start();
		let textTween = this.add.tween(this.teamtext1).to( { alpha:0 }, 500, "Linear", true, 0, 0);
		textTween.start();
		let footballInScaleTween = this.add.tween(this.teamGroup1.scale).to( {x:0.45, y:0.45 }, 500, "Linear", true, 0, 0);
		footballInScaleTween.start();

		// Move each team to next stage for results page and hide text
		let footballInTween2 = this.add.tween(this.teamGroup2).to( { y: this.game.world.centerY -32, x:this.game.world.centerX +170 }, 500, "Linear", true, 100, 0);
		footballInTween2.start();
		let textTween2 = this.add.tween(this.teamtext2).to( { alpha:0 }, 500, "Linear", true, 100, 0);
		textTween2.start();
		let footballInScaleTween2 = this.add.tween(this.teamGroup2.scale).to( {x:0.45, y:0.45 }, 500, "Linear", true, 100, 0);
		footballInScaleTween2.start();

		// Move each team to next stage for results page and hide text
		let footballInTween3 = this.add.tween(this.teamGroup3).to( { y: this.game.world.centerY +53, x:this.game.world.centerX -200, alpha:1 }, 500, "Linear", true, 200, 0);
		footballInTween3.start();
		let textTween3 = this.add.tween(this.teamtext3).to( { alpha:0 }, 500, "Linear", true, 200, 0);
		textTween3.start();
		let footballInScaleTween3 = this.add.tween(this.teamGroup3.scale).to( {x:0.45, y:0.45 }, 500, "Linear", true, 200, 0);
		footballInScaleTween3.start();

		// Move each team to next stage for results page and hide text
		let footballInTween4 = this.add.tween(this.teamGroup4).to( { y: this.game.world.centerY +138, x:this.game.world.centerX +170, alpha:1 }, 500, "Linear", true, 300, 0);
		footballInTween4.onComplete.add(function(){
			// Go to next game state - D Results with no score page
			this.game.state.start('GameStateD');
		}, this);
		footballInTween4.start();

		let textTween4 = this.add.tween(this.teamtext4).to( { alpha:0 }, 500, "Linear", true, 300, 0);
		textTween4.start();
		let footballInScaleTween4 = this.add.tween(this.teamGroup4.scale).to( {x:0.45, y:0.45 }, 500, "Linear", true, 300, 0);
		footballInScaleTween4.start();
	}

}

export default GameStateC;
